:host {
  display: block;
  display: flex;
  width: auto;
  justify-content: space-between;
  position: relative;
  align-items: flex-start;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  :host {
    /* IE10+ */
    display: none;
  }
}

:host([vertical]) {
  flex-direction: column;
}

:host([hidden]) {
  display: none;
}

.pfe-progress-steps__progress-bar {
  display: flex;
  position: absolute;
  top: calc( var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px)) - var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px)) + var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px)));
  left: calc(var(--pfe-progress-steps__item--size--first, 75px) / 2);
  height: var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px));
  width: calc(100% - var(--pfe-progress-steps__item--size--first, 75px) / 2 - var(--pfe-progress-steps__item--size--last, 75px) / 2);
  background-color: var(--pfe-progress-steps__progress-bar--color, var(--pfe-theme--color--ui--border--lighter, #d2d2d2));
}

:host([vertical]) .pfe-progress-steps__progress-bar {
  width: var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px));
  top: calc( var( --pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px) ) / 2);
  left: calc( (var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px)) / 2) + var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px)));
  height: calc(100% - var( --pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px) ) - 1em);
}

.pfe-progress-steps__progress-bar--fill {
  display: block;
  height: var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px));
  background-color: var(--pfe-progress-steps__progress-bar--color--active, var(--pfe-theme--color--feedback--info, #06c));
  width: 0;
}

:host([vertical]) .pfe-progress-steps__progress-bar--fill {
  width: var(--pfe-progress-steps__progress-bar--thickness, var(--pfe-theme--ui--border-width--md, 2px));
  height: 0;
}
